<template>
    <div class="table-list-page">
        <techo-list ref="list" :title="title" :header="header" :data="data" :isShowCode="isShowCode" :disCodeOption="disCodeOption" :recordCount="recordCount"
          @onAdd="handleAdd" @onPageChange="handlePageChange" @onDelete="handleDelete" @onView="handleView" @onEdit="handleEdit"
          @onTreeNode="handleTreeNode">
            <div slot="header">
                <el-row type="flex" justify="space-around" class="techo-list__header-row">
                    <el-col :span="10">
                        <span class="techo-list__header-col-txt-5">综治中心名称：</span>
                        <div class="techo-list__header-col-left-5">
                            <el-input placeholder="请输入综治中心名称" :maxlength="10" size="small" class="techo-list__header-select-full" clearable v-model="queryForm.name"></el-input>
                        </div>
                    </el-col>
                    <el-col :span="9">
                    </el-col>
                    <el-col :span="5" class="techo-list__header-col-button">
                        <el-button size="small" class="techo-list__header-button-reset" @click="handleClear">清空</el-button>
                        <el-button size="small" class="techo-list__header-button-query" @click="handleQuery">查询</el-button>
                    </el-col>
                </el-row>
            </div>
        </techo-list>
        <techo-details v-if="dlgVisible" ref="details" :configKey="configKey" :title="title"></techo-details>
        <vAddDialog ref="vAddDialog"></vAddDialog>
    </div>
</template>

<script>
import service from "./services/zz_service.js";
import vAddDialog from "./zz-center-add.vue";

export default {
  components: {
    vAddDialog
  },
  data() {
    return {
      title: "综治中心",
      header: {
        综治中心名称: "name",
        综治中心联系方式: "contact",
        // 综治中心层级: "dicMap.hierarchy",
        负责人姓名: "leaderName",
        负责人联系方式: "leaderContact",
        // 组成单位: "module",
        // 所在地: "dicMap.locality",
        所在地详址: "localityDetail",
        // 区域编码: "districtCode"
      },
      data: [],
      recordCount: 0,
      queryForm: {
        name: "",
        districtCode: ""
      },
      disCodeOption: [],
      isShowCode: ["6102"], // 数默认展开第一级
      // 详情页面是否显示
      dlgVisible: false,
      configKey: "center"
    };
  },

  mounted() {
    service.requestGridCode(data => {
      this.disCodeOption = [data];
      this.isShowCode = [data.code];
      this.queryForm.districtCode = data.code;
    });
  },

  watch: {
    queryForm: {
      handler(newName, oldName) {
        this.handleQuery();
      },
      deep: true
    }
  },

  methods: {
    /** 点击行政区树 返回行政区编码 */
    handleTreeNode(node) {
      this.queryForm.districtCode = node.code;
    },
    /** 添加综治中心 */
    handleAdd() {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = true;
      this.$refs["vAddDialog"].title = "添加" + this.title;
      this.$refs["vAddDialog"].dialogForm.districtCode = this.queryForm.districtCode;
    },
    /** 删除综治中心 */
    handleDelete(tableSelection) {
      let that = this;
      this.$popup.showLoading("正在删除中，请稍候...");
      new Promise((resolve, reject) => {
        let count = 0;
        _.map(tableSelection, (item, index) => {
          service.requestDeleteCenter(item.id, data => {
            count++;
            count == tableSelection.length && resolve();
          });
        });
      })
        .then(() => {
          that.$popup.showNotification({
            type: "success",
            message: "综治中心删除成功！"
          });
          that.handleQuery();
          that.$popup.closeLoading();
        })
        .catch(() => {
          that.$popup.showNotification({
            type: "warning",
            message: "综治中心删除失败！"
          });
          that.$popup.closeLoading();
        });
    },
    /** 页码改变后执行的函数 */
    handlePageChange() {
      this.data = [];
      this.handleQuery();
    },
    /** 查看信息 */
    handleView(index, row) {
      this.dlgVisible = true;
      this.$nextTick(function() {
        this.$refs["details"].data = row;
        this.$refs["details"].visible = true;
      });
    },
    /** 修改信息 */
    handleEdit(index, row) {
      this.$refs["vAddDialog"].dialogVisible = true;
      this.$refs["vAddDialog"].isAdd = false;
      this.$refs["vAddDialog"].title = "修改" + this.title;
      this.$refs["vAddDialog"].dialogForm = _.cloneDeep(row);
    },
    /** 清空查询条件 */
    handleClear() {
      this.queryForm = {
        name: "",
        districtCode: constants.REGION.CODE
      };
    },
    /** 查询列表 */
    handleQuery() {
      let pageInfo = this.$refs["list"].getPageInfo();
      pageInfo = { ...pageInfo, ..._.cloneDeep(this.queryForm) };
      service.requestPageCenter(pageInfo, data => {
        if (data.totalCount != 0 && data.list.length == 0) {
          pageInfo.page = data.totalPage;
          service.requestPageCenter(pageInfo, data => {
            this.recordCount = data.totalCount;
            this.data = data.list;
          });
        } else {
          this.recordCount = data.totalCount;
          this.data = data.list;
        }
      });
    }
  }
};
</script>
