<template>
    <el-dialog class="add-center-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="150px">
            <el-row :gutter="15">
              <el-col :span="12">
                <el-form-item label="综治中心名称" prop="name">
                    <el-input v-model="dialogForm.name" placeholder="请填写综治中心名称"></el-input>
                </el-form-item>
                <el-form-item label="综治中心层级" prop="hierarchy">
                    <el-select placeholder="请选择综治中心层级" clearable v-model="dialogForm.hierarchy">
                        <el-option v-for="(item, index) in dic['机构层级']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="负责人姓名" prop="leaderName">
                    <el-input v-model="dialogForm.leaderName" placeholder="请填写负责人姓名"></el-input>
                </el-form-item>
                <el-form-item label="所在地" prop="locality">
                    <el-select placeholder="请选择居住地" clearable v-model="dialogForm.locality">
                        <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>                        
                    </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="综治中心联系方式" prop="contact">
                    <el-input v-model="dialogForm.contact" placeholder="请填写综治中心联系方式"></el-input>
                </el-form-item>  
                <el-form-item label="组成单位" prop="module">
                    <el-input v-model="dialogForm.module" :maxlength="400" placeholder="请填写组成单位"></el-input>
                </el-form-item>
                <el-form-item label="负责人联系方式" prop="leaderContact">
                    <el-input v-model="dialogForm.leaderContact" placeholder="请填写负责人联系方式"></el-input>
                </el-form-item>
                <el-form-item label="所在地详址" prop="localityDetail">
                    <el-input v-model="dialogForm.localityDetail" placeholder="请填写所在地详址"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
        </el-form>
        <techo-position ref="position" :code="dialogForm.districtCode" :placement="'right'" @formLonLat="getLonlat"></techo-position>
        <div class="lon-lat">
            <span>经度：</span>
            <span>{{ dialogForm.x }}</span>
            <span>经度：</span>
            <span>{{ dialogForm.y }}</span>
        </div>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/zz_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dialogForm: {
        x: "",
        y: "",
        districtCode: "",
        locality: "610000",
        localityDetail: ""
      },
      teamRules: {
        name: [
          {
            required: true,
            message: "请输入综治中心名称",
            trigger: "blur"
          },
          {
            min: 2,
            max: 25,
            message: "长度在 2 到 25 个字符",
            trigger: "blur"
          }
        ],
        contact: [
          {
            required: true,
            message: "请输入综治中心联系方式",
            trigger: "blur"
          },
          util.validate.tellAndPhone()
        ],
        hierarchy: [
          {
            required: true,
            message: "请选择综治中心层级",
            trigger: "change"
          }
        ],
        leaderName: [
          {
            required: true,
            message: "请输入负责人姓名",
            trigger: "blur"
          },
          {
            min: 2,
            max: 12,
            message: "长度在 2 到 12 个字符",
            trigger: "blur"
          }
        ],
        leaderContact: [
          {
            required: true,
            message: "请输入负责人联系方式",
            trigger: "blur"
          },
          util.validate.tellAndPhone()
        ],
        module: [
          {
            message: "请输入组成单位",
            trigger: "blur"
          }
        ],
        locality: [{ message: "请选择所在地", trigger: "change" }]
      },
      dic: {
        机构层级: [],
        行政区划: []
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  watch: {
    "dialogForm.districtCode": {
      handler(code) {
        if (code) {
          service.requestCodeByAddress(code, data => {
            if (data.belongname) {
              this.dialogForm.localityDetail = data.belongname + data.name;
            } else {
              this.dialogForm.localityDetail = "铜川市";
            }
          });
        }
      }
    },
    isAdd(value) {
      if (value == false) {
        this.$nextTick(() => {
          this.$refs["position"].addMarker({
            lng: this.dialogForm.x,
            lat: this.dialogForm.y
          });
        });
      }
    }
  },

  methods: {
    getLonlat(value) {
      this.dialogForm.x = value.lng;
      this.dialogForm.y = value.lat;
    },
    /** 保存 */
    submitForm() {
      if (this.dialogForm.x == undefined || this.dialogForm.x == "") {
        this.$popup.showNotification({
          type: "warning",
          message: "请选择地理位置！"
        });
      } else {
        this.$refs["dialogForm"].validate(valid => {
          if (valid) {
            let param = _.cloneDeep(this.dialogForm);
            param.dicMap && delete param.dicMap;
            if (this.isAdd == true) {
              // 添加
              service.requestSaveCenter(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "综治中心添加成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            } else if (this.isAdd == false) {
              // 修改
              service.requestUpdateCenter(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "综治中心修改成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.$refs["position"].mapApi.clear();
      this.$refs["position"].mapApi.mapOper.setFullExtent();
      this.dialogForm = {
        x: "",
        y: "",
        districtCode: "",
        locality: "610000",
        localityDetail: ""
      };
    }
  }
};
</script>
<style>
.add-center-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-center-dialog .el-select {
  width: 100%;
}
.add-center-dialog .el-cascader {
  width: 100%;
}
.add-center-dialog .lon-lat {
  display: inline-block;
  padding: 0 10px;
  font-size: 16px;
}
.add-center-dialog .lon-lat span {
  padding-right: 10px;
}
</style>